.span {
	font-size: 110px;
	font-weight: bold;
}
.span_wrapper{
  margin-bottom: 90px;
}

.img {
  margin-bottom: 300px;
  margin-left: calc(50% - 9px);
}

.wrapper_img {
  width: 3300px;
}

.timer{
    position: absolute;
    top: 50%;
    left: 50%;
    height: 60%;
    width: 100%;
    text-align: center;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
}